* {
	margin: 0;
}

h1 {
	text-align: center;
}

.board {
	display: flex;
	border: 1px solid blue;
	justify-content: space-evenly;
}

.col {
	width: 300px;
	border: 1px solid black;
		h3 {
			border: 2px solid black;
		}

		div {
			padding: 20px;
		}
}

.tiles {
	width:  32px;
	height: 32px;
	position: absolute;
	left: calc(50% - 448px);
	top: calc(50% - 400px);
}

.selected {
	box-sizing: border-box;
	border: 1px solid lightblue;
}

#ValidateButton {
	width: 40px;
	position: absolute;
	left: calc(50% - 40px);
	top: calc(50% + 405px);
}